<script>
    import {
        uiHelper,
        formValidator
    } from '@/util'
    import {
        SHARED
    } from '@/constants/constants'
    import deleteIcon from '@/assets/svg/delete-icon.svg'
    import cloneDeep from 'lodash/cloneDeep'
    import filter from 'lodash/filter'
    import find from 'lodash/find'
    import map from 'lodash/map'
    import orderBy from 'lodash/orderBy'
    import {
        REFERRAL_GET_DOWNLINES,
        REFERRAL_GET_REPORT
    } from "@/store/referral.module";
    export default {
        name: 'referralReport',
        components: {
            deleteIcon
        },
        data: () => ({
            picker:{},
            dateRange: {
                startDate: '',
                endDate: ''
            },
            localdate: {
                direction: 'ltr',
                format: 'mm/dd/yyyy',
                separator: ' - ',
                applyLabel: 'Apply',
                cancelLabel: 'Cancel',
                weekLabel: 'W',
                customRangeLabel: 'Custom Range',
                daysOfWeek: ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'],
                monthNames: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
                firstDay: 0
            },
            dateOfBirthMenu: false,
            language: uiHelper.getLanguage(),
            currentCurrency: uiHelper.getCurrency(),
            validator: formValidator,
            shared: SHARED,
            selectedTab: 'tier',
            dates: ['', ''],
            headers: [{
                    text: 'memberID',
                    align: 'start',
                    sortable: false,
                    value: 'member_id',
                },
                {
                    text: 'memberCode',
                    value: 'membercode',
                    sortable: false,
                },
                {
                    text: 'registerDate',
                    value: 'register_date_time',
                    sortable: true,
                    sortBy: 'asc'
                },
                {
                    text: 'firstDepositDate',
                    value: 'login_date_time',
                    sortable: false,
                },
                {
                    text: 'lastDepositDate',
                    value: 'last_deposit_date_time',
                    sortable: false,
                },
                {
                    text: 'affiliateLevel',
                    value: 'level',
                    sortable: true,
                    sortBy: 'asc'
                },
                {
                    text: 'dateSearch',
                    value: ''
                }
            ],
            downlines: [],
            filteredDownlines: [],
        }),
        created() {
          this.metaTag()
            this.getReferralDownlines()
        },
        computed: {
            dateRangeText: {
                get(){
                    let startDate = this.dates[0] ? this.dates[0] : ''
                    let endDate = this.dates[1] ? this.dates[1] : ''
                    return startDate  + ' - ' + endDate
                },
                set(dateRange){
                    this.dates = []
                }
            },
            isLoggedIn() {
                return this.$store.state.member.isLoggedIn
            },
            referralDownlines() {
                return this.$store.state.referral.downlines
            }
        },
        watch: {
            referralDownlines() {
                this.downlines = cloneDeep(this.$store.state.referral.downlines)
                this.filterListByDate()
                this.sortTableList()
            }
        },
        methods: {
          metaTag() {
            const currentUrl = window.location.href;
            uiHelper.setCanonical(currentUrl)
            const domainWithProtocol = currentUrl.split('/')[2];
            const domain = domainWithProtocol.replace(/(^\w+:|^)\/\//, '');
            const path = window.location.pathname;
            const cleanPath = path.replace(/\/(en-BD|bn-BD)/i, '');
            const Links = `https://${domain}${cleanPath}`;
            const Linksen = `https://${domain}/en-BD${cleanPath}`;
            const Linksbn = `https://${domain}/bn-BD${cleanPath}`;
            uiHelper.setHreflangLinks(Links)
            uiHelper.setHreflangLinksen(Linksen)
            uiHelper.setHreflangLinksbd(Linksbn)
          },
            selectedDate() {
                if(this.dates[0] && this.dates[1]){
                    setTimeout(function () {
                        this.dateOfBirthMenu = false
                    }, 500);
                }
            },
            openMsgDialog(selectedMsg) {
                this.msgDialog = {
                    show: true,
                    msg: selectedMsg.message,
                    title: selectedMsg.subject
                }
            },
            closeMsgDialog() {
                this.msgDialog = {
                    show: false
                }
            },
            getReferralReports() {
                if (this.isLoggedIn)
                    this.$store.dispatch(`${REFERRAL_GET_REPORT}`)
            },
            getReferralDownlines() {
                if (this.isLoggedIn)
                    this.$store.dispatch(`${REFERRAL_GET_DOWNLINES}`)
            },
            sortTableList(obj) {
                let sortBy = filter(this.headers, function(d) {
                    return d.sortable;
                });
                if (obj) {
                    let selected = find(sortBy, function(d) {
                        if (d.value == obj.value) {
                            d.sortBy = d.sortBy == 'asc' ? 'desc' : 'asc'
                            return true;
                        }
                    });
                }
                this.filteredDownlines = orderBy(this.filteredDownlines, map(sortBy, 'value'), map(sortBy, 'sortBy'));
            },
            filterListByDate () {
                if(this.dates[0] && this.dates[1]){
                    var startDate = new Date(this.dates[0]);
                    var endDate = new Date(this.dates[1]);
                  endDate.setHours(23, 59, 59, 999)
                    this.filteredDownlines = this.downlines.filter(function(r) {
                        let date = new Date(r.register_date_time)
                        return date >= startDate && date <= endDate
                    });
                } else {
                    return this.filteredDownlines =  cloneDeep(this.downlines)
                }

            },
            clearFilter () {
                this.dates = []
                this.filterListByDate()
            }
        }
    }
</script>

